// export default Modal;
import { fetchNoteById } from '@/lib/api';

type Props = {
  params: Promise<{ id: string }>;
};

const NotePreview = async ({ params }: Props) => {
  const { id } = await params;
  const note = await fetchNoteById(id);

  return (
    <>
      <h2>{note}</h2>
      <p>{note.content}</p>
    </>
  );
};

export default NotePreview;